.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: black;
}

.content .header {
  width: 100vw;
  max-height: 100px;
  background: black;
  padding: 10px 0 10px 0;
  height: 100px;
  display: flex;
  justify-content: space-evenly;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 823px) and (-webkit-device-pixel-ratio: 3) {
  .content .header {
    padding: 10px 10px 10px 10px;
    width: auto;
  }
}

.content .header .logo {
  padding: 0;
}

.content .header .logo img.logo {
  width: 100px;
}

.content .header .top-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content .header .top-menu ul {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.content .header .top-menu ul li {
  list-style-type: none;
  margin: 20px;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  width: 100%;
}

.main .main-content {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  min-width: 100vw;
  align-items: center;
  padding: 30px 0;
  width: 100%;
}

.snap-assist .main .main-content {
  padding: 0;
}

.main .app-header {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.main .main-content .logo {
  margin: 30px;
}

.main .main-content .logo img {
  width: 90px;
  height: 90px;
}

.main .main-content .logo .placeholder {
  height: 54px;
}

.snap-assist .main .description {
  margin-bottom: 70px;
}

.main .description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  max-width: 500px;
}

.main .description .download {
  margin-top: 20px;
}

.main .main-content .preview {
  display: flex;
  padding: 20px;
}

.main .main-content .preview img {
  height: 100%;
  width: 100%;
}

.main .main-content .preview {
  display: block;
}

.snap-assist .main .main-content .preview-mobile {
  transform: translateX(0%) scale(0.5);
}

.main .main-content .preview-mobile {
  display: none;
}

.main .main-content .preview-mobile img {
  width: 100%;
  height: 100%;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 200px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 823px) and (-webkit-device-pixel-ratio: 3) {
  .footer {
    max-height: 100%;
  }
}

.footer ul {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.footer ul li {
  list-style-type: none;
  margin: 20px;
}

a {
