@media only screen and (min-device-width: 320px) and (max-device-width: 823px) and (-webkit-device-pixel-ratio: 3) {
  .main .main-content {
    flex-direction: column;
  }

  .main .description {
    width: calc(100% - 40px);
  }

  .main .main-content .preview-mobile {
    display: flex;
  }

  .main .main-content .preview {
    display: none;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 737px) {
  .main .main-content {
    flex-direction: column;
  }

  .main .description {
    width: calc(100% - 40px);
  }

  .main .main-content .preview-mobile {
    display: flex;
  }

